import React, { Component } from "react";
import PropTypes from "prop-types";

import st from "./style";

class BulletPagination extends Component {
  
  constructor(props) {
    super(props);
    this.state = {};
  }

  onClickPage = ($index) => {
    this.props.onClickPage($index);
  }

  render() {

    const Bullet = () => {
      return <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
        <circle cx="6" cy="6" r="5" stroke={this.props.bulletColor} strokeWidth="2"/>
      </svg>
    }

    const Ex = () => {
      return <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path fillRule="evenodd" clipRule="evenodd" d="M9.91305 0.3772C10.1385 0.142068 10.4476 0.00661667 10.7723 0.000608709C11.097 -0.00539926 11.4108 0.118527 11.6447 0.345159C11.8684 0.582196 11.9893 0.898867 11.9809 1.22557C11.9726 1.55227 11.8356 1.86226 11.6002 2.0874L7.69091 5.99083L11.601 9.89506C11.7221 10.0042 11.8199 10.137 11.8884 10.2854C11.9569 10.4338 11.9947 10.5947 11.9995 10.7582C12.0043 10.9218 11.9759 11.0846 11.9162 11.2368C11.8564 11.389 11.7666 11.5273 11.652 11.6435C11.5374 11.7596 11.4006 11.8511 11.2498 11.9123C11.099 11.9735 10.9374 12.0033 10.7748 11.9997C10.6122 11.9961 10.452 11.9593 10.304 11.8915C10.156 11.8237 10.0233 11.7263 9.91385 11.6053L5.99106 7.68982L2.06907 11.6053C1.84355 11.8406 1.53432 11.9762 1.20942 11.9823C0.884524 11.9883 0.570557 11.8642 0.336583 11.6373C0.112964 11.4003 -0.00795421 11.0836 0.000406423 10.7569C0.00876706 10.4302 0.145723 10.1202 0.381169 9.89506L4.29042 5.99083L0.381169 2.0874C0.145723 1.86226 0.00876706 1.55227 0.000406423 1.22557C-0.00795421 0.898867 0.112964 0.582196 0.336583 0.345159C0.570557 0.118281 0.884524 -0.00580032 1.20942 0.000208496C1.53432 0.00621731 1.84355 0.141824 2.06907 0.3772L5.99106 4.29344L9.91305 0.3772Z" fill="#F067A6"/>
      </svg>
    }

    const Pag = props => {
      if(props.index === this.props.selectedIndex){
        return <Ex />
      }
      else{
        return <div onClick={()=>{this.onClickPage(props.index)}}>
          <Bullet />
        </div>
      }
    }

    const getItems = () => {
      let arr = [];
      for(let i = 0; i<this.props.numItems; i++){
        arr.push(<div style={st.ctnPag} key={i}>
          <Pag index={i} key={i}/>
        </div>)
      }
      return arr;
    }

    return (
      <div style={{...st.container,...this.props.style}}>
        {getItems()}
      </div>
    );
  }
}

BulletPagination.propTypes = {
  selectedIndex:PropTypes.number,
  numItems:PropTypes.number,
  onClickPage:PropTypes.func,
  bulletColor:PropTypes.string,
  style: PropTypes.object,
};
BulletPagination.defaultProps = {
  selectedIndex: 0,
  numItems: 0,
  onClickPage:()=>{},
  bulletColor: 'white',
  style: {},
};

export default BulletPagination;

